
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class Winner extends Component {

    static contextType = AppContext;

    constructor(props) {
        super(props);

        this.state = {
            winner: null
        };
    }

    componentDidMount() {

        const winner = this.context.getFinalResult().reduce((target, current) => {
            if (target === null) {
                return current;
            }

            if (current.correct === target.correct) {
                return null;
            }

            return current.correct > target.correct ? current : target;
        }, null);

        this.setState({winner});
    }

    renderWinner() {

        if (this.state.winner === null) {
            return <React.Fragment>
                <div className="winner">Berabere</div>
                <img src={require('../img/result-text-2.png')} alt="" className="winner-text-2" /></React.Fragment>;
        }

        return <React.Fragment>
            <div className="winner">{this.state.winner.teamName}</div>
            <img src={require('../img/result-text-1.png')} alt="" className="winner-text-1" /></React.Fragment>;
    }

    render() {
        return (
          <div className="container">
            <div className="prev"><Link to="/"><img src={require('../img/home.png')} alt="" /></Link></div>
            {this.renderWinner()}
          </div>
        );
    }
}

export default Winner;
