
import { createContext } from 'react';


class AppContext {

    categories = [
        {
            id: '1',
            title: 'KENDNE HAS ZELLKLER',
              questions: ['FLTRESZ', 'DOAL', 'ZENGN LEZZET', 'KKL', 'ZGN', 'MAYA']
        },
        {
            id: '2',
            title: 'DOAL VE ZENGN LEZZET PENDE',
            questions: ['M', 'RTEL', 'SOUK', 'YUVARLAMAK', 'HARMANLAMAK', 'KPK']
        },
        {
            id: '3',
            title: 'FLTRESZ RETM YOLCULUU',
            questions: ['FABRKA', 'MALT', 'ARPA', 'TAZE', 'ERBETOTU', 'FII']
        },
        {
            id: '4',
            title: 'FLTRESZ OLUNCA LEZZET ARTAN EYLER',
            questions: ['BRA', 'YAZMAK', 'SYLEMEK', 'DNMEK', 'ELENMEK', 'LHAM VERMEK']
        }
    ];


    currentTeam = 0;
    currentPlayer = 0;

    teamNames = ['Takm 1', 'Takm 2'];
    completedCategories = [];
    answers = [];

    reset() {
        this.currentTeam = 0;
        this.currentPlayer = 0;

        this.teamNames = ['Takm 1', 'Takm 2'];
        this.completedCategories = [];
        this.answers = [];
    }

    setTeamNames(teamNames) {
        this.teamNames = teamNames;
    }

    getTeamNames() {
        return this.teamNames;
    }

    setCurrentTeam(currentTeam) {
        this.currentTeam = currentTeam;
    }

    getCurrentTeam() {
        return this.currentTeam;
    }

    setCurrentPlayer(currentPlayer) {
        this.currentPlayer = currentPlayer;
    }

    getCurrentPlayer() {
        return this.currentPlayer;
    }

    getCurrentTeamName() {
        return this.teamNames[this.currentTeam];
    }

    isCategoryCompleted(category) {
        return this.completedCategories.indexOf(category) !== -1;
    }

    addCompletedCategory(category) {

        if (this.currentPlayer === 0) {

            if (this.currentTeam === 0) {
                this.currentTeam = 1;
            } else {
                this.currentPlayer = 1;
                this.currentTeam = 0;
            }

        } else {

            if (this.currentTeam === 0) {
                this.currentTeam = 1;
            } else {
                this.currentPlayer = 0;
                this.currentTeam = 0;
            }

        }

        this.completedCategories.push(category);
    }

    getCategoryById(id) {
        return this.categories.find(category => category.id === id);
    }

    getResult() {

        const result = [];

        for (let player = 0; player < 2; player++) {
            for (let team = 0; team < 2; team++) {

                const teamName = this.teamNames[team];

                result.push({
                    team, player, teamName: teamName.length < 10 ? teamName : (teamName.substring(0, 10) + '...') ,
                    correct: this.answers.filter(answer => answer.type === 2 &&
                        answer.team === team && answer.player === player).length});
            }
        }

        return result;
    }

    getFinalResult() {

        const result = [];

        for (let team = 0; team < 2; team++) {

            const teamName = this.teamNames[team];

            result.push({
                team, teamName: teamName.length < 10 ? teamName : (teamName.substring(0, 10) + '...'),
                correct: this.answers.filter(answer => answer.type === 2 &&
                    answer.team === team).length});
        }

        return result;
    }

    answer(type) {
        this.answers.push({
            team: this.currentTeam,
            player: this.currentPlayer,
            type
        });
    }

    hasFinalResult() {
        return this.categories.length === this.completedCategories.length;
    }
}

export default createContext(new AppContext());
