
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

const rgNameValidator = /[^\s]+/;

class TeamNames extends Component {

    static contextType = AppContext;

    constructor(props) {
      super(props);

      this.state = {
          teamLeft: '',
          teamRight: ''
      };
    }

    handleTeamLeftChange = (event) => {
      this.setState({teamLeft: event.target.value});
    };

    handleTeamRightChange = (event) => {
      this.setState({teamRight: event.target.value});
    };

    next = () => {
        const {teamLeft, teamRight} = this.state;
        this.context.setTeamNames([teamLeft, teamRight]);
        this.props.history.push('/kategoriler');
    };

    render() {
        return (
          <div className="container">
            <div className="takim_adi">Takm Ad</div>
              <div className="takim_input">
            <input className="takim_i" type="text" value={this.state.teamLeft} onChange={this.handleTeamLeftChange} placeholder="Takm 1" />
            <input className="takim_i" type="text" value={this.state.teamRight} onChange={this.handleTeamRightChange} placeholder="Takm 2" />
              </div>

            <div className="prev"><Link to="/baslangic"><img src={require('../img/back.png')} alt="" /></Link></div>
            <div className="next">{rgNameValidator.test(this.state.teamLeft) && rgNameValidator.test(this.state.teamRight) ?
            <input width="131" type="image" src={require('../img/next.png')} onClick={this.next} alt="" /> : null}</div>
          </div>
        );
    }
}

export default TeamNames;
