
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class Index extends Component {

    static contextType = AppContext;

    componentDidMount() {
        this.context.reset();
    }

    render() {
        return (
          <div className="container">
            <h1 className="splash-logo"><img width="300px" src={require('../img/logo.png')} alt="" /></h1>
            <br /><img src={require('../img/t01.png')} alt="" />

            <br /><br /><br /><br />
            <Link to="/baslangic"><img src={require('../img/basla.png')} alt="" /></Link>
          </div>
        );
    }
}

export default Index;
