
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';


class CategoryStart extends Component {

    static contextType = AppContext;

    getCategoryId()  {
        return this.props.match.params.id;
    }

    getCategoryName() {
        return this.context.getCategoryById(this.getCategoryId()).title;
    }

    getTeamName() {
        return this.context.getCurrentTeamName();
    }

    render() {
        return (
          <div className="container">
            <div className="takim_adi">SELEN KATEGOR</div>
            <div className="cat_a">{this.getCategoryName()}</div>

            <div className="prev"><Link to="/kategoriler"><img src={require('../img/back.png')} alt="" /></Link></div>
            <div className="next"><Link to={'/oyun/' + this.getCategoryId()}><img src={require('../img/baslat.png')} alt="" /></Link></div>

            <div className="oynayan">Oynayan: {this.getTeamName()}</div>
          </div>
        );
    }
}

export default CategoryStart;
