
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class Categories extends Component {

    static contextType = AppContext;

    getCategoryList() {
        return (<div className="cat_c">
            {this.context.categories.map( category => <React.Fragment  key={category.id}>{this.context.isCategoryCompleted(category.id) ?
                <div className="cat cat-completed"><div className="cat_t">{category.title}</div></div>
            : <div className="cat"><Link to={'/kategori/' + category.id}>{category.title}</Link></div>}</React.Fragment>)}
        </div>);
    }

  render() {
    return (
      <div className="container">
      <div className="takim_adi">KATEGORN SE</div>
        <div className="prev"><Link to="/"><img src={require('../img/home.png')} alt="" /></Link></div>
        {this.getCategoryList()}
      </div>
    );
  }
}

export default Categories;
