
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class Game extends Component {

    static contextType = AppContext;

    constructor(props) {
        super(props);

        this.state = {
            timer: null,
            countdown: 0,
            timeout: false,
            questions: []
        };
    }

    componentDidMount() {
        const category = this.context.getCategoryById(this.props.match.params.id);
        this.setState({questions: category.questions.slice()});
    }

    start = () => {
        this.setState({
            countdown: 10,
            timer: setInterval(() => !this.state.timeout && this.setState(prevState => {

                if (prevState.countdown === 0) {
                    return {timeout: true};
                }

                return {countdown: prevState.countdown - 1};

            }), 1000)
        });
    };

    getTimeControl() {
        if (this.state.timer === null) {
            return <div><div className="prev"><Link to="/"><img src={require('../img/home.png')} alt="" /></Link></div><div className="next"><input type="image" src={require('../img/anlat.png')} onClick={this.start} alt="" /></div></div>;
        }

        return <div className="countdown">{this.state.countdown}</div>;
    }

    getQuestion() {
        const {questions} = this.state;
        return questions.length === 0 ? null : <div className="word_w">{questions[0]}</div>;
    }

    getTeamName() {
        return this.context.getCurrentTeamName();
    }

    answer(state, type) {

        this.context.answer(type);

        const questions = state.questions.slice(1);

        if (questions.length === 0) {
            clearInterval(state.timer);
            this.context.addCompletedCategory(this.props.match.params.id);
            this.props.history.push('/sonuc');
        }

        return questions;
    }

    onAnswer = (event) => {

        const {value} = event.target;

        this.setState(prevState => {
            return {countdown: 10, timeout: false,
                questions: this.answer(prevState, parseInt(value))};
        });

    };

    render() {
        return (
          <div className={'container' + (this.state.timeout ? ' timeout' : '')}>
            {this.getTimeControl()}
            <div className="word">{this.getQuestion()}</div>
            <div className="word_t"><img src={require('../img/t02.png')} alt="" />
            </div>
            {this.state.timer === null ? null : <div className="butons">


                <input className="g_bt" type="image" value="0" src={require('../img/yanlis.png')} onClick={this.onAnswer} alt="" />
                <input className="g_bt g_bt_pas" type="image" value="1" src={require('../img/pas.png')} onClick={this.onAnswer} alt="" />
                <input className="g_bt" type="image" value="2" src={require('../img/dogru.png')} onClick={this.onAnswer} alt="" />


            </div>}
            <div className="oynayan">Oynayan: {this.getTeamName()}</div>
          </div>
        );
    }
}

export default Game;
