import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class FinalResult extends Component {

    static contextType = AppContext;

    constructor(props) {
        super(props);

        this.state = {
            result: []
        };
    }

    componentDidMount() {
        this.setState({result: this.context.getFinalResult()});
    }

    renderTable() {
        if (this.state.result.length === 0) {
            return null;
        }

        return <div className="tablo_f"><table>

            <tbody>{this.state.result.map(item =>
                <tr key={item.team}>
                    <th className="takim-adi">{item.teamName}</th>
                    <td className="soru-sayisi">12</td>
                    <td className="dogru-sayisi">{item.correct}</td>
                </tr>)}</tbody>
        </table></div>;
    }


    render() {
        return (
          <div className="container">
          <div className="r_final">
            {this.renderTable()}
            </div>
            <div className="next"><Link to="/kazanan"><img src={require('../img/next.png')} alt="" /></Link></div>
          </div>
        );
    }
}

export default FinalResult;
