
import React, { Component } from 'react';
import { HashRouter as Router, Route } from "react-router-dom";

import Index from './pages/Index.js';
import Intro from './pages/Intro.js';
import TeamNames from './pages/TeamNames.js';
import Categories from './pages/Categories.js';
import CategoryStart from './pages/CategoryStart.js';
import Game from './pages/Game.js';
import Result from './pages/Result.js';
import FinalResult from './pages/FinalResult.js';
import Winner from './pages/Winner.js';

import AppContext from './context.js';

import './App.css';

class App extends Component {

    static contextType = AppContext;

    render() {
        return <Router>
            <div id="container">
                <Route path="/" exact component={Index} />
                <Route path="/baslangic" component={Intro} />
                <Route path="/takim-adi" component={TeamNames} />
                <Route path="/kategoriler" component={Categories} />
                <Route path="/kategori/:id" component={CategoryStart} />
                <Route path="/oyun/:id" component={Game} />
                <Route path="/sonuc" component={Result} />
                <Route path="/final-sonuc" component={FinalResult} />
                <Route path="/kazanan" component={Winner} />
            </div>
        </Router>;
    }
}

export default App;
