
import React, { Component } from 'react';

import { Link } from "react-router-dom";

import AppContext from '../context.js';

class Result extends Component {

    static contextType = AppContext;

    constructor(props) {
        super(props);

        this.state = {
            result: [],
            hasFinalResult: false
        };
    }

    componentDidMount() {
        this.setState({
            result: this.context.getResult(),
            hasFinalResult: this.context.hasFinalResult()
        });
    }

    renderTable() {
        if (this.state.result.length === 0) {
            return null;
        }

        return <div className="tablo"><table>
            <tbody>{this.state.result.map(item =>
                <tr key={item.player + '' + item.team}>
                    <th className="takim-adi">{item.teamName}</th>
                    <td className="soru-sayisi">6</td>
                    <td className="dogru-sayisi">{item.correct}</td>
                </tr>)}</tbody>
        </table></div>;
    }


    render() {
        return (
          <div className="container">
          <div className="r_sheet">
            {this.renderTable()}
          </div>
            <div className="next"><Link to={this.state.hasFinalResult ? '/final-sonuc' : '/kategoriler'}><img src={require('../img/next.png')} alt="" /></Link></div>

          </div>
        );
    }
}

export default Result;
